	module.exports = {"data":{"simple":{"frontmatter":{"title":"privacy policy"},"html":"<p>You’ll notice this privacy policy is short, simple and in plain language. Data collection, where needed, is minimized in accordance to lean data practices and choices are provided to you. This is a model for other organizations to follow.</p>\n<p>The Glass Room is a free pop-up exhibit sponsored by Mozilla and Tactical Technology Collective.  The exhibit features speakers, workshops, art installations, demonstrations, and gadgets to facilitate public dialogue on the role of data in our digital lives.</p>\n<h2>Things you should know</h2>\n<p>We use Piwik to better understand how many people visit this website, what webpages they navigate to, and how they interact with those webpages (like event registrations, downloads, email sign-ups, and clicks to related content).</p>\n<ul>\n<li>The way that we use Piwik ensures that visitor data to this website is shared only with Mozilla.</li>\n<li>If you have Do Not Track enabled, your data is not transmitted to Piwik.</li>\n</ul>\n<p>You can optionally register for Glass Room workshops on this website.</p>\n<ul>\n<li>Your name and email address are required for registration. This data is shared with Mozilla.</li>\n</ul>\n<p>You can optionally sign-up for emails from Mozilla to receive related updates, news and opportunities.</p>\n<ul>\n<li>If you sign-up for Mozilla’s newsletter your email address, country, and language preference will be shared with Mozilla and its email provider, Salesforce.</li>\n</ul>"}},"pathContext":{"slug":"/privacy"}}

